* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    line-height: 1.4;
    background-color: #fcfcfc;
    color: #1f1f1f;
}

.container {
    max-width: 500px;
    margin: 0 auto;
    border: 1px solid var(--main-color);
}
.todo-item {
    list-style-type: none;
    padding: 10px 15px;
    border-top: 1px solid var(--main-color);
}

.todo-item input {
    margin-right: 15px;
}

.todo-item span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 80%;
    height: 18px;
    display: inline-block;
}

.header-container {
    background-color: var(--main-color);
    color: #fff;
    padding: 10px 15px;
}

.header-title {
    font-size: 25px;
    line-height: 1.447em;
    margin: 0;
}

.completed {
    font-style: italic;
    color: #c5e2d2;
    text-decoration: line-through;
}

.btn-style {
    background: #d35e0f;
    color: #fff;
    border: 1px solid #d35e0f;
    padding: 3px 7px;
    border-radius: 50%;
    cursor: pointer;
    float: right;
    outline: none;
}

.form-container {
    display: flex;
    width: 100%;
    }
    
    .input-text {
        flex: 8;
        font-size: 14px;
        padding: 6px 15px;
        background-color: rgba(103, 140, 137, 0.65);
        border: none;
        color: #fff;
        outline: none;
        font-weight: 400;
        width: 80%;
    }
    
    .input-text::placeholder {
        color: #fff;
    }
    
    .input-submit {
        flex: 2;
        border: none;
        background: #678c89;
        color: #fff;
        text-transform: uppercase;
        cursor: pointer;
        font-weight: 600;
        width: 20%;
        outline: none;
    }
    
    .footer {
        height: 50px;
        background: var(--main-color);
        color: white;
        position: relative;
        }
        
        .vertical-center {
            margin: 0 0 0 10px;
            position: absolute;
            top: 50%;
            -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}

.dot {
    height: 25px;
    width: 25px;
    border-radius: 50%;
    display: inline-block;
    margin: 0 5px;
}
.red {
    background-color: #ff0000;
}
.blue {
    background-color: #0000ff;
}
.gray {
    background-color: #678c89;
}

:root {
    --main-color: #678c89;
}